@import "./vars";

#Skills {
  box-sizing: border-box;
  display: block;
  width: 100%;
  margin: 10px 0;
  padding: 0 25px;
  overflow: hidden;

  .info {
    box-sizing: border-box;
    display: block;

    &>div {
      display: inline-block;
      width: 100%;
      box-sizing: border-box;

      .info-flex {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-bottom: 5px;

        svg {
          padding-right: 10px;
        }
      }

      .info-title {
        font-size: 1.2rem;
        margin-bottom: 10px;
        .info-icon{
          color: $link;
        }
      }

      .info-content {
        box-sizing: border-box;
        margin-bottom: 35px;

        .content-item {
          margin: 5px 0;

          .skill-text {
            text-align: center;
            font-size: 0.9rem;
            padding: 5px 25px;
            color: #fff;
            line-height: 1.3rem;
            font-family: Verdana, Geneva, Tahoma, sans-serif;
          }
        }
      }
    }
  }

  @media only screen and (max-width: 960px) {
    padding: 0px;

    .info {
      &>div {
        .info-content {
          padding: 0 5px;

          .skill-text {
            font-size: 0.8rem !important;
          }
        }
      }
    }
  }
}