@import "./vars";

#Home{
  box-sizing: border-box;
  .about{
    a{
      color: $link;
    }
    padding: 10px 30px;
    margin: 5px 0;
  }
  .extra{
    margin: 25px 0;
    padding: 25px 30px;
    .extra-item{
      padding-left: 15px;
    }
  }
  .more{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 10px 30px;
    margin: 5px 0;
    .item{
      margin: 5px 0;
      &.chip{
        margin-bottom: 15px;
        margin-right: 3px;
      }
    }
  }
}